// *** utils ***
@import "utils/variables";

:root {
    --theme-default: #009DB5;
    --theme-secondary: #F94C8E;
    --light-background: #F6F7F9;
    --body-light-font-color: #7B8298;
    --body-font-color: #151521;
    --light-border: rgba(106, 113, 133, 0.4);
    --chart-border: #ECF3FA;
    --recent-chart-bg: #FCFCFD;
    --light-bg: rgba(242, 243, 247, 0.7);
    --white: #fff;
    --light2: #F5F6F9;
    --sidebar-border: rgba(0, 0, 0, 0.1);
    --chart-text-color: rgba(82, 82, 108, 0.8);
    --recent-dashed-border: rgba(82, 82, 108, 0.3);
    --chart-dashed-border: #ECF3FA;
    --chart-progress-light: rgba(82, 82, 108, 0.1);
    --recent-box-bg: #F2F4F7;
    --recent-border: rgba(97, 101, 122, 0.25);
    --recent-icon-box: #FEFEFF;
    --course-light-btn: #F2F3F7;
    --course-bg: #F7F7F7;
    --balance-profie-bg: #E0DFEF;
    --view-grid-bg: #ECF3FA80;
    --view-border-marker: #D0E8EB;
    --light-slate: #F1F5F9;
    --product-shadow: 0px 1px 0px rgba(82, 82, 108, 0.15);
    --light-shade-primary: rgba(0, 157, 181, 0.11);
    --slate-color: #e9e9e9;
    --lighter-border: #ddd;
    --light-primary-color: #009DB533;
    --normal-gray: #52526c;


    // --dark-primary: rgba(115, 102, 255);
    --border-color: #6A7185;
    --progress-bg: #E1ECF3;
    --gray-1: rgb(207, 220, 232);
    --light-border-color: #f1f1f1;
    --lighter-background: #f6f7fb;

    // sidebar settings	
    --sidebar-background: #1E1E2D;
    --sidebar-link-text-color: #9FA8C1;
    --sidebar-border-color: rgba(106, 113, 133, 0.2);
    --sidebar-search-bg: rgba(106, 113, 133, 0.2);
    // bootstrap variables	
    --bs-heading-color: #151521;
    --bs-form-invalid-border-color: #F6463A;

    --fc-button-bg-color: var(--theme-default);
    --fc-button-active-bg-color: var(--theme-default);
    --fc-button-active-border-color: var(--theme-default);
    --fc-button-border-color: var(--theme-default);
    --fc-button-hover-bg-color: var(--theme-default);
    --fc-button-hover-border-color: var(--theme-default);

    // bootstrap modal
    --bs-modal-header-padding: 20px;
    --bs-modal-header-border-width: 1px;
    --bs-modal-header-border-color: #dee2e6;
    // tables
    // rating 
    --normal: #ECEAF3;
    --normal-shadow: #D9D8E3;
    --normal-mouth: #9795A4;
    --normal-eye: #595861;
    --active: #F8DA69;
    --active-shadow: #F4B555;
    --active-mouth: #F05136;
    --active-eye: #313036;
    --active-tear: #76b5e7;
    --active-shadow-angry: #e94f1d;
    // bootstrap 
    --bs-list-group-border-width: 1px;
    --bs-list-group-border-color: #dee2e6;
    --bs-btn-active-border-color: #F94C8E;
    --bs-btn-active-color: var(--body-font-color);

    //  header height css
    --header-height: 76px;

    // Tagify
    /* Do not show the "remove tag" (x) button when only a single tag remains */
    .customLook {
        --tag-bg: var(--theme-default);
        --tag-hover: var(--theme-secondary);
        --tag-text-color: var(--white);
        --tags-border-color: silver;
        --tag-border-radius: 25px;
        --tag-text-color--edit: #111;
        --tag-remove-bg: var(--tag-hover);
        --tag-pad: .6em 1em;
        --tag-inset-shadow-size: 1.35em;
        --tag-remove-btn-bg--hover: #000;
        --tagify-dd-color-primary: var(var(--theme-default));
        --tagify-dd-bg-color: var(--course-light-btn);
    }

    .tagify {
        --tags-focus-border-color: #cccccc;
    }
    .table {
        --bs-body-bg: auto;
        &.table-inverse {
            --bs-table-bg: #292b2c;
        }
        &.table-striped {
            &.bg-primary {
                --bs-table-bg: unset;
            }
        }
    }
}

.dark-only {
    --light-background: rgba(31, 32, 40, 0.65);
    --recent-chart-bg: #262932;
    --light-bg: #1d1e26;
    --white: #262932;
    --sidebar-border: rgba(255, 255, 255, 0.1);
    --light2: #1d1e26;
    --chart-border: #374558;
    --recent-box-bg: #1d1e26;
    --recent-border: #374558;
    --body-font-color: rgba(255, 255, 255, 0.6);
    --normal-gray: #85859c;
    --chart-text-color: rgba(101, 101, 113, 1);
    --course-light-btn: var(--light-bg);
    --view-grid-bg: var(--light-bg);
    --view-border-marker: #2c2c45d4;
    --light-slate: #1c1d26;
    --slate-color: rgba(255, 255, 255, 0.6);
    --product-shadow: 0px 1px 0px rgb(55 70 87);
    --bs-tertiary-bg: #262932;
    --bs-border-color: #333843;
    --recent-icon-box: #1d1e26;
    --progress-bg: #262932;
    --chart-dashed-border: rgba(106, 113, 133, 0.4);
    --light-slate: #262932;

    // bootstrap variables	
    --bs-heading-color: rgba(255, 255, 255, 0.6);
    --bs-body-color: rgba(255, 255, 255, 0.6);
    --bs-body-bg: transparent;
    --bs-body-color-rgb: rgba(255, 255, 255, 0.6);
}

