/**=====================
    3.7 Comingsoon CSS Start
==========================**/
.comingsoon-bgimg {
  background: url(../images/login/login_bg.jpg);
  background-position: bottom;
  background-size: cover;
}
.comingsoon {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 50px 0;
  .comingsoon-inner {
    img {
      height: 40px;
    }
    h5 {
      font-size: 18px;
      letter-spacing: 1px;
      color: $theme-body-sub-title-color;
      font-weight: 500;
      margin-bottom: 20px;
      margin-top: 20px;
    }
    .countdown {
      padding: 20px 0;
      border-top: 1px solid $light-color;
      border-bottom: 1px solid $light-color;
      .time {
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        color: $white;
        font-weight: 500;
        width: calc(50px + (100 - 50) * ((100vw - 320px) / (1920 - 320)));
        height: calc(50px + (100 - 50) * ((100vw - 320px) / (1920 - 320)));
        font-size: calc(16px + (36 - 16) * ((100vw - 320px) / (1920 - 320)));
        background: var(--theme-default);
      }
      .title {
        padding-top: 13px;
        font-size: 14px;
        font-weight: 400;
        color: $theme-body-sub-title-color;
        text-transform: uppercase;
        letter-spacing: 1px;
      }
      ul li {
        display: inline-block;
        text-transform: uppercase;
        margin: 0 20px;
        span {
          display: block;
        }
      }
    }
  }
  video {
    position: fixed;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -100;
    transform: translateX(-50%) translateY(-50%);
    transition: 1s opacity;
  }
}
/**=====================
  3.7 Comingsoon CSS Ends
==========================**/