/**=====================
    3.25 Knowledgebase CSS start
==========================**/
.browse {
  .browse-articles {
    span {
      vertical-align: middle;
      &:nth-child(2) {
        padding-left: 20px;
        [dir="rtl"] & {
          padding-left: unset;
          padding-right: 20px;
        }
      }
      svg {
        width: 14px;
        height: 14px;
        vertical-align: text-top;
        position: absolute;
        left: 0;
        top: 4px;
      }
    }
    h5 {
      padding-bottom: 10px;
      margin-bottom: 0px;
      border-bottom: 1px solid var(--recent-dashed-border);
      padding-left: 25px;
      span {
        svg {
          width: 16px;
          height: 16px;
          vertical-align: text-top;
          top: 0;
          left: 7px;
        }
      }
    }
    ul {
      li {
        padding: 8px;
        border-radius: 8px;
        a {
          color: $theme-body-font-color;
          position: relative;
          display: flex;
          align-items: center;
          gap: 10px;
        }
        &:hover {
          background-color: $light;
        }
      }
    }
  }
}
@media only screen and (max-width: 1366px) {
  .browse {
    div[class^="col-"] {
      &:nth-child(3) {
        margin-top: 30px;
      }
    }
  }
}
@media only screen and (max-width: 360px) {
  .browse {
    .browse-articles {
      ul {
        li {
          a {
            span {
              &:nth-child(2) {
                width: 158px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                display: inline-block;
              }
            }
          }
        }
      }
    }
  }
}
.badge {
  &.pull-right {
    padding-top: 6px;
  }
}
.articles-icons {
  svg {
    min-width: 18px;
  }
}
.d-flex {
  &.faq-widgets {
    svg {
      min-width: 18px;
    }
  }
}
.knowledgebase-contents {
  .browse-articles {
    ul {
      li {
        span {
          &:nth-child(2) {
            min-width: 100px;
            overflow: hidden;
            white-space: nowrap;
            max-width: 390px;
            text-overflow: ellipsis;
          }
          svg {
            color: var(--normal-gray);
          }
        }
      }
    }
  }
}
/**=====================
    3.25 Knowledgebase CSS End
==========================**/