/**=====================
   5.5 Search CSS Start
==========================**/
.search-page {
  .theme-form {
    input {
      border: 1px solid var(--theme-default);
      padding-left: 20px;
      border-radius: 10px 0 0 10px;
      flex: 1 1 auto;
      @media (max-width:420px) {
        width: 175px !important;
      }
      &::placeholder {
        color: var(--theme-default);
      }
      &:focus {
        outline: none;
      }
    }
    .input-group {
      .btn {
        line-height: 32px;
        font-size: 16px;
      }
      .form-control-plaintext {
        background: transparent;
      }
    }
  }
  h6 {
    line-height: 1.4;
  }
  .nav-link {
    font-weight: 500;
    text-transform: uppercase;
  }
  .border-tab {
    &.nav-tabs {
      .material-border {
        border-bottom-width: 1px;
      }
      .nav-item {
        .nav-link {
          font-size: 15px;
          padding: 10px 18px;
        }
      }
    }
  }
  .d-flex {
    .flex-grow-1 {
      overflow: hidden;
    }
  }
  .info-block {
    padding: 20px;
    border-radius: 15px;
    border: 1px solid var(--chart-dashed-border);
    flex-wrap: wrap;
    gap: 14px;
    a {
      color: var(--body-font-color);
      margin-bottom: 3px;
      display: block;
    }
    h6 {
      color: var(--theme-default);
    }
    +.info-block {
      margin-top: 20px;
    }
  }
  .border-tab.nav-tabs {
    align-items: left;
    justify-content: end;
    .nav-item {
      width: auto;
    }
  }
  .search-links {
    h6 {
      margin-bottom: 10px;
    }
  }
  p {
    text-transform: lowercase;
    margin-bottom: 0;
    color: $light-text;
  }
  ul.search-info {
    li {
      display: inline-block;
      font-size: 12px;
      line-height: 1;
      color: var(--body-light-font-color);
      +li {
        border-left: 1px solid lighten($dark-color, 50%);
        padding-left: 8px;
        margin-left: 8px;
      }
      i {
        color: $warning-color;
        &:not(:last-child) {
          margin-right: 4px;
        }
      }
    }
  }
  #video-links {
    .embed-responsive+.embed-responsive {
      margin-top: 30px;
    }
  }
}
.search-list {
  margin-bottom: 20px;
  width: auto;
  display: inline-flex;
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid var(--theme-default);
  border: none;
  .nav-item {
    background-color: rgba($primary-color, 0.1);
    .nav-link {
      border: none;
      padding: 0.7rem 1.5rem;
      opacity: 0.5;
      cursor: pointer;
      color: $white !important;
      &.active {
        background-color: rgba($primary-color, 0.1);
        color: var(--theme-default);
        opacity: 1;
        border-radius: 0;
      }
    }
    &.show,
    &.active {
      background-color: rgba($primary-color, 0.1);
    }
  }
}
/**=====================
  5.5 Search CSS Ends
==========================**/