/**=====================
  5.7 Sidebar CSS Start
==========================**/
%sidebar-after {
  position: absolute;
  content: "";
  top: 4px;
  width: 13px;
  height: 15px;
  border-bottom-left-radius: 12px;
  border-bottom: 2px solid rgba($white, 0.4);
  transform: rotate(357deg);
  [dir="rtl"] & {
    border-bottom-left-radius: unset;
    border-bottom-right-radius: 12px;
  }
}
%sidebar-before {
  position: absolute;
  content: '';
  width: 1px;
  height: 0;
  background: var(--body-light-font-color);
  top: -10px;
  left: 28px;
  transition: height 0.5s;
  [dir="rtl"] & {
    left: unset;
    right: 28px;
  }
}
%horizontal-after {
  position: absolute;
  content: "";
  top: 49%;
  width: 5px;
  height: 1px;
  border-radius: 100%;
  background-color: #59667a;
}
%for-animated-hover {
  box-shadow: $main-header-shadow;
  transform: translateY(30px);
  opacity: 0;
  visibility: hidden;
  left: 0;
  [dir="rtl"] & {
    left: unset;
    right: 0;
  }
}
%for-animated-hover-box {
  opacity: 1;
  transform: translateY(0px);
  visibility: visible;
  border-radius: 5px;
  overflow: hidden;
  &:before {
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 7px solid $white;
    content: "";
    top: -7px;
    position: absolute;
    left: 10px;
    z-index: 2;
    [dir="rtl"] & {
      left: unset;
      right: 10px;
    }
  }
  &:after {
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 7px solid $light-background;
    content: "";
    top: -7px;
    position: absolute;
    left: 10px;
    z-index: 1;
    [dir="rtl"] & {
      left: unset;
      right: 10px;
    }
  }
}
.page-wrapper {
  position: relative;
  .page-header {
    max-width: 100vw;
    position: $main-header-position;
    top: $main-header-top;
    z-index: $main-header-z-index;
    transition: $sidebar-transition;
    background-color: var(--sidebar-background);
    @media (max-width:767px) {
      position: unset;
    }
    @keyframes wave {
      0% {
        background-position-x: 100vw;
      }
      100% {
        background-position-x: 200vw;
      }
    }
    .header-wrapper {
      width: 100%;
      align-items: center;
      padding: $header-wrapper-padding;
      position: unset;
      &.open {
        z-index: 4;
      }
      @media (max-width:767px) {
        position: fixed;
        z-index: 3;
      }
      .search-full {
        background-color: $white;
        position: absolute;
        right: 0;
        z-index: 1;
        top: 0;
        height: 0;
        width: 0;
        transform: scale(0);
        transition: all 0.3s ease;
        [dir="rtl"] & {
          right: unset;
          left: 0;
        }
        &.open {
          height: 100%;
          width: 100%;
          animation: zoomIn 0.5s ease-in-out;
          transform: scale(1);
          transition: all 0.3s ease;
          z-index: 2;
          @media (max-width:767px) {
            z-index: 5;
          }
        }
        input {
          line-height: 65px;
          padding-left: 60px;
          width: 100% !important;
          [dir="rtl"] & {
            padding-right: 60px;
            padding-left: unset;
          }
          &:focus {
            outline: none !important;
          }
        }
        .form-group {
          .close-search {
            position: absolute;
            font-size: 15px;
            right: 30px;
            top: 26px;
            color: $dark-gray;
            cursor: pointer;
            [dir="rtl"] & {
              left: 30px;
              right: unset;
            }
          }
          &:before {
            position: absolute;
            left: 30px;
            top: 27px;
            content: "\f002";
            font-size: 16px;
            color: $dark-gray;
            font-family: FontAwesome;
            [dir="rtl"] & {
              left: unset;
              right: 30px;
            }
          }
        }
      }
      .nav-right {
        ul {
          li {
            svg {
              vertical-align: $header-wrapper-nav-icon-align;
              width: $header-wrapper-nav-icon-size;
              height: $header-wrapper-nav-icon-size;
              stroke: var(--body-font-color);
              fill: none;
              path {
                color: $header-wrapper-nav-icon-color;
              }
            }
            .mode {
              .moon-icon {
                display: none;
              }
              .sun-icon {
                display: block;
              }
              &.active {
                .moon-icon {
                  display: block;
                }
                .sun-icon {
                  display: none;
                }
              }
            }
          }
        }
        &.right-header {
          ul {
            li {
              .mode {
                cursor: pointer;
                text-align: center;
                i {
                  font-size: 18px;
                  font-weight: 600;
                  transition: all 0.3s ease;
                  &.fa-lightbulb-o {
                    font-weight: 500;
                    font-size: 22px;
                    transition: all 0.3s ease;
                    animation: zoomIn 300ms ease-in-out;
                  }
                }
              }
              .header-search {
                cursor: pointer;
              }
            }
          }
        }
        >ul {
          display: flex;
          align-items: center;
          justify-content: flex-end;
          >li {
            padding: 6px 10px;
            display: inline-block;
            position: relative;
            .badge {
              position: absolute;
              right: -5px;
              top: -6px;
              padding: 2px 4px;
              font-size: 11px;
              font-weight: 700;
              font-family: Inter, sans-serif;
              [dir="rtl"] & {
                right: unset;
                left: -5px;
              }
            }
            span {
              ul {
                left: inherit;
                right: -10px;
                width: 130px;
                [dir="rtl"] & {
                  left: -10px;
                  right: inherit;
                }
                &:before,
                &:after {
                  left: inherit;
                  right: 10px;
                  [dir="rtl"] & {
                    left: 10px;
                    right: inherit;
                  }
                }
                li {
                  display: block;
                  a {
                    font-size: 14px;
                    color: $dark-color;
                    i {
                      margin-left: 10px;
                      font-size: 13px;
                      color: $dark-color;
                      [dir="rtl"] & {
                        margin-left: unset;
                        margin-right: 10px;
                      }
                    }
                  }
                }
              }
            }
          }
          .profile-nav {
            .onhover-click {
              display: flex;
              align-items: center;
              gap: 8px;
            }
            .sidebar-image {
              position: relative;
              img {
                width: 40px;
                height: 40px;
                @media (max-width:575px) {
                  width: 36px;
                  height: 36px;
                }
              }
            }
            .sidebar-content {
              @media (max-width: 767px) {
                display: none;
              }
            }
            .status {
              width: 10px;
              height: 10px;
              border-radius: 100%;
              position: absolute;
              right: -2px;
              bottom: -2px;
              border: 2px solid var(--body-font-color);
              [dir="rtl"] & {
                right: unset;
                left: 0;
              }
              @media (max-width:575px) {
                width: 8px;
                height: 8px;
              }
            }
          }
          .profile-dropdown {
            width: 160px;
            top: 57px;
            left: -12px;
            padding: 6px 0;
            [dir="rtl"] & {
              left: unset;
              right: -12px;
            }
            li {
              padding: 6px 12px;
              &:last-child {
                padding-bottom: 6px;
              }
              a {
                text-decoration: unset;
                display: flex;
                align-items: center;
                gap: 10px;
                .profile-icon {
                  width: 30px;
                  height: 30px;
                  background: var(--light-slate);
                  border-radius: 5px;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
              }
              +li {
                margin-top: 0;
              }
              span {
                color: var(--body-font-color);
                text-transform: capitalize;
              }
              svg {
                width: 16px;
                vertical-align: bottom;
                stroke: var(--body-font-color);
              }
              &:hover {
                background-color: rgba($primary-color, 0.1);
                a {
                  span {
                    color: var(--theme-default);
                    transition: color 0.3s ease;
                  }
                  .profile-icon {
                    background-color: var(--white);
                  }
                  svg {
                    stroke: var(--theme-default);
                    transition: stroke 0.3s ease;
                  }
                }
              }
            }
          }
          .chat-dropdown {
            right: -50px;
            [dir="rtl"] & {
              right: unset;
              left: -50px;
            }
            @media (max-width: 575px) {
              right: 15px;
              [dir="rtl"] & {
                right: unset;
                left: 15px;
              }
            }
            .dropdown-title {
              .d-flex {
                gap: 10px;
              }
              .status {
                width: 8px;
                height: 8px;
                border-radius: 100%;
              }
            }
            li {
              gap: 10px;
              padding: 10px 0;
              +li {
                margin-top: 0;
              }
              h6 {
                a {
                  color: var(--body-font-color);
                  text-decoration: unset;
                  text-transform: capitalize;
                  line-height: 1.5;
                }
              }
              &:last-child {
                padding: 10px 0;
              }
              >div {
                display: flex;
                align-items: center;
                gap: 5px;
                >div {
                  padding: 5px 10px;
                  background: var(--light-slate);
                }
              }
              &.send-msg {
                >div {
                  >div {
                    border-radius: 8px 8px 8px 0;
                  }
                }
              }
              &.reply-msg {
                >div {
                  flex-direction: row-reverse;
                  >div {
                    border-radius: 8px 8px 0 8px;
                  }
                }
              }
            }
            .message-icon {
              min-width: 36px;
              height: 36px;
              display: flex;
              align-items: center;
              justify-content: center;
              border-radius: 100%;
              svg {
                width: 20px;
                height: 20px;
                stroke: $white;
              }
            }
            .chat-input {
              padding: 0 20px 20px;
              .form-control,
              .input-group-text {
                border: none;
                svg {
                  stroke: $white;
                }
              }
              .form-control {
                background: var(--light-slate);
                padding: 10px 20px;
                &::placeholder {
                  color: var(--body-font-color);
                }
              }
            }
          }
          .flag-icon {
            font-size: 16px;
          }
        }
        .bookmark-flip {
          width: unset !important;
          box-shadow: unset !important;
          background-color: transparent !important;
          overflow: initial;
          &::after,
          &::before {
            display: none;
          }
          .bookmark-dropdown {
            .bookmark-content {
              text-align: center;
              .bookmark-icon {
                width: 52px;
                height: 52px;
                background-color: #f9f9f9;
                border-radius: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 auto;
              }
              span {
                font-size: 12px;
                font-weight: 500;
                letter-spacing: 0.5px;
              }
            }
          }
        }
        .flip-card {
          width: 300px;
          height: 260px;
          background-color: transparent;
          .flip-card-inner {
            position: relative;
            height: 100%;
            text-align: center;
            transition: transform 0.6s;
            transform-style: preserve-3d;
            .front,
            .back {
              position: absolute;
              backface-visibility: hidden;
              border-radius: 10px;
              overflow: hidden;
              box-shadow: 0 0 20px rgba(89, 102, 122, 0.1);
              width: 300px;
              height: 260px;
              button {
                background-color: $transparent-color;
                color: var(--theme-default);
                border: none;
                &:focus {
                  outline: $transparent-color;
                }
              }
            }
            .front {
              background-color: $white;
              transition: .3s;
              svg {
                stroke: $dark-color;
                transition: all 0.3s ease;
                &:hover {
                  stroke: var(--theme-default);
                  transition: all 0.3s ease;
                }
              }
            }
            .back {
              background-color: $white;
              transform: rotateY(180deg);
              li {
                &:first-child {
                  margin-top: 20px;
                  height: 140px !important;
                }
              }
              button {
                width: 100%;
              }
              .flip-back-content {
                input {
                  border-radius: 25px;
                  padding: 6px 12px;
                  width: 100%;
                  border: 1px solid #ddd;
                  &:focus {
                    outline: none;
                  }
                }
              }
            }
            &.flipped {
              transform: rotateY(180deg);
              .front {
                opacity: 0;
              }
            }
          }
        }
        .onhover-show-div {
          width: 330px;
          top: 50px;
          right: 0;
          left: unset;
          [dir="rtl"] & {
            right: unset;
            left: 0;
          }
          .dropdown-title {
            padding: 20px;
            border-bottom: 1px solid rgba($dropdown-border-color, 0.3);
            @media (max-width: 575px) {
              padding: 15px;
            }
          }
          ul {
            padding: 15px 20px;
            @media (max-width: 575px) {
              padding: 15px;
            }
          }
          li {
            padding: 15px;
            p {
              font-size: 14px;
              letter-spacing: 0.3px;
              margin-bottom: 0;
            }
            a {
              text-decoration: none;
              text-transform: uppercase;
              letter-spacing: 0.3px;
            }
            &+li {
              margin-top: 15px;
            }
            &:last-child {
              padding-bottom: 15px;
            }
            .d-flex {
              position: relative;
              img {
                width: 40px;
                position: relative;
              }
              .status-circle {
                left: 0;
                [dir="rtl"] & {
                  left: unset;
                  right: 0;
                }
              }
              .flex-grow-1 {
                >span {
                  letter-spacing: 0.8px;
                  padding-right: 10px;
                  display: inline-block;
                }
                p {
                  margin-bottom: 8px;
                }
              }
            }
          }
          &:before,
          &:after {
            right: 10px !important;
            left: unset !important;
            [dir="rtl"] & {
              right: unset !important;
              left: 10px !important;
            }
          }
        }
        .notification-box {
          position: relative;
        }
        .cart-box {
          position: relative;
        }
        .cart-dropdown {
          ul {
            padding: 0 0 15px;
          }
          li {
            margin: 0 20px;
            padding: 10px 0;
            @media (max-width: 575px) {
              margin: 0 15px;
            }
            .d-flex {
              position: relative;
              align-items: center;
              .flex-grow-1 {
                margin-right: 20px;
                flex: 1;
                [dir="rtl"] & {
                  margin-left: 20px;
                  margin-right: unset;
                }
                @media (max-width: 575px) {
                  margin-right: 15px;
                  [dir="rtl"] & {
                    margin-left: 15px;
                    margin-right: unset;
                  }
                }
                [dir="rtl"] & {
                  margin-right: unset;
                  margin-left: 20px;
                  @media (max-width: 575px) {
                    margin-right: 20px;
                    margin-left: unset;
                  }
                }
              }
              .img-60 {
                height: 60px;
                object-fit: cover;
              }
            }
            &+li {
              margin-top: 0;
              border-top: 1px solid rgba($dropdown-border-color, 0.1);
            }
            &:last-child {
              border-top: none;
            }
          }
          .cart-product {
            &.product-remove {
              display: none;
            }
          }
          p {
            line-height: 1.3;
            margin-top: 3px;
            opacity: 0.6;
          }
          .close-circle {
            position: absolute;
            right: 0;
            top: 0;
            [dir="rtl"] & {
              right: unset;
              left: 0;
            }
            a {
              width: 18px;
              height: 18px;
              border-radius: 100%;
              display: flex;
              align-items: center;
              justify-content: center;
            }
            svg {
              width: 14px;
              stroke: $danger-color;
            }
          }
          .qty-box {
            margin: 5px 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .touchspin-wrapper {
              width: 108px;
              background-color: var(--light-slate);
              padding: 5px;
              border-radius: 25px;
              .btn-touchspin {
                background: var(--white);
                border: none;
                border-radius: 100%;
                width: 26px;
                height: 26px;
                i {
                  font-size: 10px;
                }
              }
              input {
                font-size: 12px;
                font-weight: 700;
                height: 20px;
                background-color: transparent;
                max-width: 38px;
              }
            }
          }
          .total {
            margin: 0;
            padding: 15px;
            background-color: #f9f9f9;
            border: none;
            h6 {
              font-weight: 500;
              font-size: 12px;
              line-height: 14px;
              letter-spacing: 0.5px;
              span {
                font-size: 14px;
              }
            }
          }
          .view-checkout {
            text-decoration: unset;
            font-weight: 600;
          }
        }
        .notification-box {
          svg {
            animation: swing 1.5s ease infinite;
          }
        }
        .notification-dropdown {
          top: 52px;
          width: 300px;
          right: -20px !important;
          left: unset;
          [dir="rtl"] & {
            left: -20px !important;
            right: unset !important;
          }
          @media (max-width: 575.98px) {
            width: calc(100vw - 30px);
          }
          li {
            gap: 15px;
            padding: 0;
            .notification-image {
              position: relative;
              img {
                width: 50px;
                max-height: 50px;
                border-radius: 100%;
              }
              .notification-icon {
                width: 18px;
                height: 18px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 100%;
                position: absolute;
                right: 0;
                top: 30px;
                [dir="rtl"] & {
                  left: 0;
                  right: unset;
                }
                i {
                  font-size: 10px;
                }
              }
            }
            &:last-child {
              text-align: center;
              background-color: transparent;
            }
          }
          &:before,
          &:after {
            right: 28px !important;
            left: unset !important;
            [dir="rtl"] & {
              left: 28px !important;
              right: unset !important;
            }
          }
        }
      }
    }
    .profile-nav {
      display: flex;
      align-items: center;
      gap: 8px;
    }
  }
  .page-body-wrapper {
    background-color: $main-body-color;
    .page-body {
      min-height: calc(100vh - #{$header-size});
      margin-top: $header-size;
      padding: $page-body-padding;
      position: relative;
      transition: 0.5s;
      &:before {
        animation: fadeIn 0.5s ease-in-out;
      }
    }
    div.sidebar-wrapper {
      .sidebar-main {
        .sidebar-links {
          .simplebar-wrapper {
            .simplebar-mask {
              .simplebar-content-wrapper {
                .simplebar-content {
                  >li {
                    a {
                      svg {
                        &.stroke-icon {
                          stroke: var(--white);
                        }
                      }
                      &.active {
                        svg {
                          transition: all 0.3s ease;
                          &.stroke-icon {
                            stroke: var(--theme-default);
                          }
                        }
                      }
                    }
                  }
                  li {
                    a {
                      text-transform: capitalize;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .profile-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .profile-wrapper {
      display: flex;
      gap: 10px;
      align-items: center;
      .status {
        width: 10px;
        height: 10px;
        border: 2px solid var(--sidebar-background);
        bottom: -2%;
      }
    }
    div {
      h4 {
        font-weight: 600;
        color: var(--slate-color);
      }
      span {
        color: var(--body-light-font-color);
        font-size: 12px;
        font-weight: 600;
      }
    }
    div {
      svg {
        width: 22px;
        height: 22px;
        stroke: var(--body-light-font-color);
      }
    }
  }
  .sidebar-search {
    padding: 20px;
    border-bottom: 1px solid var(--sidebar-border-color);
    z-index: 5;
    position: relative;
    .form-control,
    .input-group-text {
      background-color: var(--sidebar-search-bg);
      border: none;
    }
    .form-control {
      padding: 10px 12px 10px 0;
      color: $white;
      &::placeholder {
        color: rgba($white, 0.6);
      }
      &:focus {
        box-shadow: unset;
      }
    }
    svg {
      width: 18px;
      height: 18px;
      stroke: var(--slate-color);
    }
    .input-group> :not(:first-child):not(.dropdown-menu):not(.valid-tooltip):not(.valid-feedback):not(.invalid-tooltip):not(.invalid-feedback) {
      margin-left: 0;
    }
  }
  .sidebar-wrapper {
    background-color: var(--sidebar-background);
    color: var(--white);
  }
  &.horizontal-wrapper {
    [data-simplebar] {
      position: relative;
    }
    .header-logo-wrapper {
      &.col-auto {
        &.left-header {
          display: none;
        }
      }
    }
    .header-left-wrapper {
      .toggle-sidebar {
        display: none;
      }
      .logo-wrapper {
        img {
          height: 30px;
        }
      }
    }
    .header-right-wrapper {
      >div {
        h2 {
          color: var(--sidebar-link-text-color);
        }
      }
      &.page-title {
        ol {
          .breadcrumb-item {
            &:nth-child(3) {
              color: var(--sidebar-link-text-color) !important;
            }
          }
        }
      }
    }
    .header-wrapper {
      .nav-right {
        >ul {
          li {
            svg {
              stroke: var(--white);
            }
          }
          .profile-nav {
            .sidebar-content {
              h4 {
                color: var(--sidebar-link-text-color);
              }
            }
          }
        }
      }
      .search-full {
        background-color: var(--sidebar-background);
        &.open {
          .form-group {
            .form-control-plaintext {
              color: var(--body-light-font-color) !important;
            }
          }
        }
      }
    }
    .footer {
      margin-left: 0;
